// CLIPBOARD CONTROL PANEL

#ccp {
  position: relative;
  width: 98%;
  height: 530px;
  margin: 6px auto 0;
  padding: 0;
  transform: scaleX(0);
  transition: transform 100ms;
  border: 1px solid #161616;
  border-radius: 10px;
  background: #000000;
  opacity: .91;
  z-index: 100;
  cursor: default;
  // -webkit-transform: scaleX(0);
  // -webkit-transition: -webkit-transform 500ms;
  // Animation
  // Get rid of vendor prefixes
  &.active {
    // :VA Fix the vendor prefixes
    // -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
  h2 {
    display: block;
    margin: 8px 0 0 0;
    padding: 0;
    color: $primary-font-color;
    font-size: 14px;
    text-align: center;
  }
}

.ccp-link {
  color: #057cc6;
  font-weight: bold;
  text-decoration: underline;
}

#ccp-content {
  // VA! Reboot: Working
  display: flex;
  position: relative;
  flex-direction: column;
  width: 100%;
  height: 500px;
  // margin: 0 auto;
  padding: 0 10px;
  // Character styles for CCP user input elements
  input, label {
    display: inline-block;
    max-width: 100%;
    color: #FFFFFF;
    font-size: 14px;
    font-weight: 300;
  }
  select {
    width: 60px;
    font-size: 14px;
  }
  input {
    width: 150px;
    border: 1px solid #616161;
    background: #000000;
    color: #FFFFFF;
  }

}



// VA! Set the width of the 4 options columns in each fieldset
fieldset {
  // VA! Reboot - Applying flex to fieldset didn't work
  display: block;
  position: relative;
  width: 100%;
  // VA! Below overrides the fieldset margin in normalize.less
  margin: 0;
  // VA! Below overrides the fieldset padding in normalize.less
  padding: 0;
  margin-bottom: 3px;
  border: 1px solid $fieldset-border-color;
  border-radius: 5px;
  legend {
    width: 145px;
    height: 20px;
    margin: 0 0 0 20px;
    padding: 0 10px;
    border: none;
    color: $fieldset-legend-color;
    font-size: 16px;
    font-weight: 400;
  }
  &#ccp-img-options {
    height: 140px;
    .ccp-options-container {
      height: 119px;
    }
  }
  &#ccp-td-options {
    height: 190px;
    .ccp-options-container {
      height: 169px;
    }
  }
  &#ccp-table-options {
    height: 160px;
    .ccp-options-container {
      height: 139px;
    }
  }
}

