.ccp-img-include-anchor-container input, 
.ccp-table-include-wrapper-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;

}

/* Create a custom checkbox -- this is the span after the checkbox input element. */
#spn-ccp-img-include-width-height-checkmrk, 
#spn-ccp-img-include-anchor-checkmrk, 
#spn-ccp-table-include-wrapper-checkmrk {
  position: absolute;
  top: 3px;
  // left: 40px;
  height: 15px;
  width: 15px;
  background-color: #eee;
  display: block;
  z-index: 100;

}

/* On mouse-over, add a grey background color */
.ccp-img-include-width-height-container:hover input ~ #spn-ccp-img-include-anchor-checkmrk, 
.ccp-img-include-anchor-container:hover input ~ #spn-ccp-img-include-anchor-checkmrk, 
.ccp-table-include-wrapper-container:hover input ~ #spn-ccp-table-include-wrapper-checkmrk {
  background-color: #ccc;

}
/* When the checkbox is checked, add a gray background */
.ccp-img-include-width-height-container input:checked ~ #spn-ccp-img-include-width-height-checkmrk, 
.ccp-img-include-anchor-container input:checked ~ #spn-ccp-img-include-anchor-checkmrk, 
.ccp-table-include-wrapper-container input:checked ~ #spn-ccp-table-include-wrapper-checkmrk {
  background-color: #5f5e5e;


}

/* Create the checkmrk/indicator (hidden when not checked) */
#spn-ccp-img-include-width-height-checkmrk:after, 
#spn-ccp-img-include-anchor-checkmrk:after, 
#spn-ccp-table-include-wrapper-checkmrk:after {
  content: "";
  position: absolute;
  display: none;

}

/* Show the checkmrk when checked */
.ccp-img-include-width-height-container input:checked ~ #spn-ccp-img-include-width-height-checkmrk:after, 
.ccp-img-include-anchor-container input:checked ~ #spn-ccp-img-include-anchor-checkmrk:after, 
.ccp-table-include-wrapper-container input:checked ~ #spn-ccp-table-include-wrapper-checkmrk:after  {
  display: block;

}


/* Style the checkmrk/indicator */
.ccp-img-include-width-height-container #spn-ccp-img-include-width-height-checkmrk:after, 
.ccp-img-include-anchor-container #spn-ccp-img-include-anchor-checkmrk:after, 
.ccp-table-include-wrapper-container #spn-ccp-table-include-wrapper-checkmrk:after {
  left: 5px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);

}

