figure {
  position: relative;
  margin: 15px 0 !important;
  padding: 0 0 10px;
  border-top: 1px solid $dim-color;
  border-bottom: 1px solid $whit-color;
  &.qs-clipboard-functions {
    position: relative;
    width: 100%;
    // !VA CLEARFIX! WORKS!
    &::after {
      display: block;
      clear: both;
      content: '';
    }
    .qs-clipboard-functions-left {
      display: block;
      position: relative;
      width: 50%;
      float: left;
    }
    .qs-clipboard-functions-right {
      display: block;
      position: relative;
      width: 50%;
      float: left;
    }

  }
  
  h4 {
    margin-bottom: 5px;
    font-size: 18px;
    font-style: italic;
  }
  img {
    margin-bottom: 10px;
    &.qs-inspector-content {
      width: 800px;
      height: 102px;
    }
    &.qs-image-resize-tools {
      width: 646px; 
      height: 50px;
    }
    &.qs-ccp-but {
      width: 85px;
      height: 50px;
      float: left;
      margin: 0 20px 10px 0;
    }
  }
  figcaption, aside {
    margin-top: 5px;
    font-size: 16px;
    line-height: 19px;
    &.qs-ccp-but-caption {
      min-height: 60px;
      margin-bottom: 15px;
      padding-right: 50px;
    }
  }
 

  img {
    &.qs-viewer-width-icon {
      vertical-align: -12px;
    }
  }
  img {
    &.viewer-width-field {
      width: 146px;
      height: 50px;
    }
  }
}




